
.top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    font-weight: bold;
    padding: 20px;
    font-size: 30px;
    animation: changeColors;
    animation-duration: 0.5s;
    background-image: linear-gradient(to left, rgba(37, 92, 253, 0.85), rgba(59, 70, 236, 0.85), rgba(87, 64, 202, 0.85), rgba(108, 60, 187, 0.85), rgba(115, 0, 189, 0.85));
    ;
    z-index: 600;
}
.up{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    font-weight: bold;
    padding: 20px;
    font-size: 30px;
    background-color: white;
    color: black;
    position: sticky;
    top: 0;
    z-index: 600;
    

}
.down{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    font-weight: bold;
    padding: 20px;
    font-size: 30px;
    background-color: white;
    color: black;
    position: sticky;
    top: 0;
    animation-name: changeOpacity;
    animation-duration: 1s;
    z-index: 600;
    
}
@keyframes changeOpacity{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}
@keyframes changeColors{
    from{
        background-color: white;
        color: black;
    }
    to{
        background-color: transparent;
        color: white;
    }
}